import React from 'react'
import logoProCreditBank from '../media/logo.png'
import logoProBanking from '../media/ProBanking.png'

export const Header = props => {

  return(
    <header>
      <div className="header-wrapper">
        <div className="header-logos-container">
          <div><img src={logoProCreditBank} alt="ProCreditBank" /></div>
          <div><img src={logoProBanking} alt="ProBanking" /></div>
        </div>
        <div className="header-languages-container">
          <div className="row">
            <a href="#">Engleza</a>
            <a href="#">Romana</a>
          </div>
        </div>
        <div className="bars-container">
          <div className="red-bar" />
          <div className="color-bar-container">
            <div className="yellow bar-item" />
            <div className="orange bar-item" />
            <div className="red bar-item" />
            <div className="green bar-item" />
            <div className="violet bar-item" />
            <div className="blue bar-item" />
          </div>
        </div>
      </div>
    </header>
  )
}