import React from 'react'
import securityLogo from '../media/security.png'
import banner from '../media/banner-small.png'

export const RightPart = props => {

  return (
    <div className="security-container">
      <div className="top-security">
          <img src={securityLogo} alt="security" />
        <div className="security-content">
          <div className="security-title">
            Online Security
          </div>
          <div className="security-description">
            Please do not respond to any emails containing links to online banking websites or requesting personal details <span>(<a href="#">"phishing"</a>)</span>.
          </div>
          <div className="security-description">
           If you notice any <span><a href="#">suspicious emails</a></span> or transactions, contact ProCredit Bank.
          </div>
          <div className="security-description">
            Please read the <span><a href="#">security</a></span> information we provide on the left.
          </div>
        </div>
      </div> 
      <div className="ssl-container">
        <a href="#">ABOUT SSL CERTIFICATES</a>
      </div>
      <div className="banner-container">
        <img src={banner} alt="banner" />
      </div>                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          
    </div>
  )
}